// è½»é‡çº§ Markdown æ¸²æŸ“ç»„ä»¶
const SimpleMarkdown = ({ text }: { text: string }) => {
  if (!text) return null;
  return (
    <div className="whitespace-pre-wrap leading-relaxed font-mono text-sm text-slate-300">
      {text.split(/(\*\*.*?\*\*)/g).map((part, index) => {
        if (part.startsWith('**') && part.endsWith('**')) {
          return <strong key={index} className="text-emerald-400 font-bold">{part.slice(2, -2)}</strong>;
        }
        return part;
      })}
    </div>
  );
};

// Helper to clean JSON string from markdown code blocks and extract valid JSON
const cleanJson = (text: string) => {
  if (!text) return text;
  // 1. Remove markdown code blocks if present
  let cleaned = text.replace(/```json\n?|\n?```/g, '');
  cleaned = cleaned.replace(/```\n?/g, '');
  
  // 2. Extract JSON object using regex (find first { and last })
  const jsonMatch = cleaned.match(/\{[\s\S]*\}/);
  if (jsonMatch) {
    return jsonMatch[0];
  }
  
  return cleaned.trim();
};

